"use client";
import { Card } from "@/components/ui/card";
import { Home, ShoppingBag, Bus, Utensils, Film, Grid2x2 } from "lucide-react";

const expensesData = [
  {
    category: "Housing",
    icon: <Home size={20} />,
    amount: 250,
    change: 15,
    up: true,
    items: [
      { name: "House Rent", amount: 230, date: "17 May 2023" },
      { name: "Parking", amount: 20, date: "17 May 2023" },
    ],
  },
  {
    category: "Food",
    icon: <Utensils size={20} />,
    amount: 350,
    change: 8,
    up: false,
    items: [
      { name: "Grocery", amount: 230, date: "17 May 2023" },
      { name: "Restaurant bill", amount: 120, date: "17 May 2023" },
    ],
  },
  {
    category: "Transportation",
    icon: <Bus size={20} />,
    amount: 50,
    change: 12,
    up: false,
    items: [
      { name: "Taxi Fare", amount: 30, date: "17 May 2023" },
      { name: "Metro Card bill", amount: 20, date: "17 May 2023" },
    ],
  },
  {
    category: "Entertainment",
    icon: <Film size={20} />,
    amount: 80,
    change: 15,
    up: false,
    items: [
      { name: "Movie ticket", amount: 30, date: "17 May 2023" },
      { name: "iTunes", amount: 50, date: "17 May 2023" },
    ],
  },
  {
    category: "Shopping",
    icon: <ShoppingBag size={20} />,
    amount: 420,
    change: 25,
    up: true,
    items: [
      { name: "Shirt", amount: 230, date: "17 May 2023" },
      { name: "Jeans", amount: 190, date: "17 May 2023" },
    ],
  },
  {
    category: "Others",
    icon: <Grid2x2 size={20} />,
    amount: 50,
    change: 23,
    up: true,
    items: [
      { name: "Donation", amount: 30, date: "17 May 2023" },
      { name: "Gift", amount: 20, date: "17 May 2023" },
    ],
  },
];

export default function ExpensesBreakdown() {
  return (
    <div className="mt-8">
      <h2 className="text-lg font-semibold text-gray-700 mb-4">
        Expenses Breakdown
      </h2>

      {/* Responsive Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
        {expensesData.map((exp, idx) => (
          <Card
            key={idx}
            className="p-4 rounded-xl shadow-sm bg-white flex flex-col"
          >
            {/* Header */}
            <div className="flex justify-between items-start mb-3">
              <div className="flex items-center gap-2">
                <div className="p-2 rounded-md bg-gray-100">{exp.icon}</div>
                <div>
                  <h3 className="text-sm font-medium text-gray-600">
                    {exp.category}
                  </h3>
                  <p className="text-lg font-bold text-gray-900">
                    ${exp.amount.toFixed(2)}
                  </p>
                </div>
              </div>
              <div className="text-right text-xs text-gray-500">
                <span
                  className={`font-semibold ${
                    exp.up ? "text-red-500" : "text-green-500"
                  }`}
                >
                  {exp.change}% {exp.up ? "↑" : "↓"}
                </span>
                <div>Compare to last month</div>
              </div>
            </div>

            {/* Items */}
            <div className="border-t pt-3 space-y-2">
              {exp.items.map((item, i) => (
                <div
                  key={i}
                  className="flex justify-between items-center text-sm"
                >
                  <div>
                    <p className="text-gray-700">{item.name}</p>
                    <p className="text-gray-400 text-xs">{item.date}</p>
                  </div>
                  <p className="font-medium">${item.amount.toFixed(2)}</p>
                </div>
              ))}
            </div>
          </Card>
        ))}
      </div>
    </div>
  );
}
